html, body{
  color: white;
  background-color: #1b1b32;
  font-family: Lato, sans-serif;
  margin-top: 40px;
}

.aplicaciones-tareas{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.tareas-lista-principal{
  width: 600px;
  min-height: 500px;

  background-color: #d0d0d5;
  padding: 25px;
  margin: 10px;
  
  border-radius: 26px;
}

h1{
  color: #1b1b32;
  font-size: 25px;
  font-family: Roboto, sans-serif;
  text-align: center;
  margin: 20px 0;
}